.main {
  &.container {
    @media (max-width: $xs) {
      padding: 0;
    }
  }

  &__grid {
    width: 100%;
    display: flex;
    flex-direction: column;
    row-gap: 20px;

    display: grid;
    display: -ms-grid;
    gap: 20px;

    grid-template-columns: 1fr;

    grid-template-areas:
      'sale'
      'tabs'
      'advantages'
      'job'
      'schedule'
      'slider'
      'record';

    @media (min-width: $xs) {
      grid-template-columns: 1fr 1fr;
      -ms-grid-columns: 1fr 1fr;

      grid-template-areas:
        'sale sale'
        'tabs tabs'
        'advantages advantages'
        'job schedule'
        'slider slider'
        'record record';
    }

    @media (min-width: $sm) {
      -ms-grid-columns: 1fr 20px 1fr 20px 1fr 20px 229px;
      grid-template-columns: 1fr 1fr 1fr 229px;

      grid-template-areas:
        'sale sale sale advantages'
        'tabs tabs tabs tabs'
        'job job schedule schedule'
        'slider slider slider slider'
        'record record record record';
    }

    @media (min-width: $md) {
      -ms-grid-columns: 1fr 20px 1fr 20px 2fr;
      grid-template-columns: 1fr 1fr 2fr;
      grid-template-areas:
        'sale sale advantages'
        'sale sale  tabs'
        'job  schedule slider'
        'record  record record';
    }

    // @media (min-width: flg) {
    //   grid-template-columns: 1fr 1fr 3fr;
    //   grid-template-areas:
    //     'sale sale advantages'
    //     'sale sale  tabs'
    //     'job  schedule slider'
    //     'record  record record';
    // }

    @media (min-width: $lg) {
      grid-template-columns: 25% 25% 1fr;
      grid-template-areas:
        'sale sale advantages'
        'sale sale  tabs'
        'job  schedule slider'
        'record  record record';
    }

    // @media (min-width: 992px) {
    //   -ms-grid-columns: 1fr 20px 1fr 20px 600px;
    //   -ms-grid-rows: 1fr 20px 1fr 20px 1fr 20px 1fr;
    //   grid-template-columns: 25% 25% 50%;

    //   grid-template-areas:
    //     'sale sale advantages'
    //     'sale sale  tabs'
    //     'job  schedule slider'
    //     'record  record record';
    // }

    @media (min-width: $xl) {
      -ms-grid-columns: 1fr 20px 1fr 20px 3fr;
      -ms-grid-rows: 1fr 20px 1fr 20px 1fr 20px 1fr;
      grid-template-columns: 1fr 1fr 3fr;

      grid-template-areas:
        'sale sale advantages'
        'sale sale  tabs'
        'job  schedule slider'
        'record  record record';

      // grid-template-areas:
      //   'sale sale advantages'
      //   'sale sale  advantages'
      //   'tabs tabs  tabs'
      //   'job  schedule slider'
      //   'record  record record';
    }

    @media (min-width: 1440px) {
      margin-top: 17px;
      gap: 15px;
      row-gap: 20px;
      // grid-template-columns: 1fr 1fr minmax(1fr, 660px);
      -ms-grid-columns: 1fr 20px 1fr 20px 660px;
      -ms-grid-rows: 1fr 100px 1fr 20px 1fr 20px 1fr;
      grid-template-columns: 1fr 1fr minmax(auto, 660px);

      grid-template-areas:
        'sale sale advantages'
        'sale sale  tabs'
        'job  schedule slider'
        'record  record record';
    }
  }

  &__sale {
    grid-area: sale;
    -ms-grid-area: sale;
    display: flex;
    flex-direction: column;

    @media (max-width: $xs) {
      padding-left: 16px;
      padding-right: 16px;
    }

    @media (min-width: $xl) {
      margin-top: 10px;

      -ms-grid-column: 1;
      -ms-grid-column-span: 3;
      -ms-grid-row: 1;
      -ms-grid-row-span: 3;
    }
  }

  &__adv {
    grid-area: advantages;
    -ms-grid-area: advantages;
    display: flex;
    flex-direction: column;

    @media (max-width: $xs) {
      padding-left: 16px;
      padding-right: 16px;
    }

    @media (min-width: $xl) {
      -ms-grid-column: 5;
      -ms-grid-row: 1;
      max-width: 100%;
    }

    & .advantages {
      height: 100%;
      width: 100%;

      @media (min-width: $lg) {
        height: auto;
      }
    }

    & .advantages__list {
      @media (min-width: $sm) {
        height: 100%;
      }
      @media (min-width: $lg) {
        height: auto;
      }
    }
  }

  &__form {
    grid-area: tabs;
    -ms-grid-area: tabs;
    display: flex;
    flex-direction: column;

    @media (max-width: $xs) {
      padding-left: 16px;
      padding-right: 16px;
    }

    @media (min-width: $xl) {
      -ms-grid-column: 5;
      -ms-grid-row: 2;
      max-width: 100%;
    }
  }

  &__job {
    grid-area: job;
    -ms-grid-area: job;
    display: flex;
    flex-direction: column;

    @media (max-width: $xs) {
      padding-left: 16px;
      padding-right: 16px;
    }
    @media (min-width: $sm) {
      margin-top: 15px;
      margin-bottom: 15px;
    }
    @media (min-width: $md) {
      margin-top: 0;
      margin-bottom: 0;
    }

    @media (min-width: $xl) {
      -ms-grid-column: 1;
      -ms-grid-row: 5;
    }

    & .job {
      height: 100%;
      width: 100%;
    }
  }

  &__schedule {
    grid-area: schedule;
    -ms-grid-area: schedule;
    display: flex;
    flex-direction: column;

    @media (max-width: $xs) {
      padding-left: 16px;
      padding-right: 16px;
    }

    @media (min-width: $sm) {
      margin-top: 15px;
      margin-bottom: 15px;
      align-items: flex-end;
    }
    @media (min-width: $md) {
      margin-top: 0;
      margin-bottom: 0;
    }

    @media (min-width: $xl) {
      align-items: start;
      -ms-grid-column: 3;
      -ms-grid-row: 5;
      max-width: 100%;
    }

    & .schedule {
      height: 100%;
      width: 100%;
    }
  }

  &__slider {
    grid-area: slider;
    -ms-grid-area: slider;
    display: flex;
    flex-direction: column;
    overflow: hidden;

    @media (min-width: $xl) {
      -ms-grid-column: 5;
      -ms-grid-row: 5;
      max-width: 100%;
    }

    & .slider {
      overflow: hidden;
      width: 100%;
      height: 100%;
      @media (max-width: $xs) {
        border-radius: 0;
      }
    }
  }

  &__record {
    grid-area: record;
    -ms-grid-area: record;
    display: flex;
    flex-direction: column;

    @media (min-width: $xl) {
      -ms-grid-column: 1;
      -ms-grid-column-span: -1;
      -ms-grid-row: 7;
    }

    @media (max-width: $xs) {
      padding-left: 16px;
      padding-right: 16px;
    }
  }
}
