.list-services {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
  padding: 1rem;

  background-color: $blue-color;

  @media (min-width: $md) {
    padding: 1.5rem;
    grid-template-columns: repeat(2, 1fr);
  }

  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 17px;
    line-height: 130%;
    text-transform: uppercase;
  }

  & ul {
    @include listReset();
  }

  & li {
    display: flex;
    flex-direction: row-reverse;
    flex-wrap: wrap;
    justify-content: start;
    align-items: center;
    margin-bottom: 5px;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 130%;

    @include ff(
      'font-size',
      (
        990px: 15px,
        1840px: 20px,
      )
    );

    @media (min-width: $md) {
      margin-bottom: 10px;
    }

    & span {
      width: calc(100% - 30px - 10px);
    }
  }

  & .icon {
    width: 30px;
    height: 30px;
    margin-right: 10px;
  }
}
