.form-offers {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  padding: 1rem;
  padding-bottom: 2rem;
  background-color: $primary-color;

  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 17px;
    line-height: 120%;
    color: $white-color;
    margin-bottom: 1rem;

    @media (min-width: $md) {
      padding: 1rem 0;
    }
  }

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;

    @media (min-width: $md) {
      display: grid;
      gap: 10px;
      grid-template-columns: repeat(2, 1fr);
      grid-template-areas:
        'l1 l2'
        'l3 bt';
    }
    @media (min-width: $xl) {
      grid-template-columns: repeat(3, 3fr) 1fr 3fr;
      grid-template-areas: 'l1 l2 l3 . bt';
    }
  }

  &__btns {
    grid-area: bt;
    display: flex;
    flex-direction: column;
    margin-top: 1.2rem;

    align-items: end;

    @media (min-width: $md) {
      align-items: stretch;
    }


  }

  & .form-label {
    // margin-bottom: 1rem;
    // font-style: normal;
    // font-weight: 400;
    // font-size: 15px;
    // line-height: 140%;

    color: $white-color;

    // @media (min-width: $md) {
    //   margin-bottom: 0.7rem;
    // }

    // @media (min-width: $xl) {
    //   margin-bottom: 1.5rem;
    // }

    &--1 {
      grid-area: l1;
    }
    &--2 {
      grid-area: l2;
    }
    &--3 {
      grid-area: l3;
    }

    // & span {
    //   display: block;
    //   margin-bottom: 5px;
    // }
  }

  & .form-label:not(:last-child) {
    @media (min-width: $md) {
      margin-right: 0;
    }
    @media (min-width: $lg) {
      margin-right: 1rem;
    }
  }

  & .form-label:not(:nth-child(2n)) {
    @media (min-width: $md) {
      margin-right: 1rem;
    }
  }

  & .form-control {
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;

    font-weight: 400;
    font-size: 20px;
    line-height: 130%;

    color: rgba(66, 71, 77, 0.5);
  }

  &__check {
    display: flex;
    align-items: center;
    margin-top: 0.5rem;
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    line-height: 140%;
    color: #8f8f8f;
    cursor: pointer;

    & span {
      padding-left: 0.3rem;
    }

    @media (min-width: $md) {
      justify-content: end;
    }
    @media (min-width: $xl) {
      justify-content: stretch;
    }
  }

  & button[type="submit"] {
    margin-left: auto;
    margin-right: 0;

    @media (min-width: $xl) {
      margin-left: 0;
    }
  }
}
