:root {
  --box-pd-base: 37px;
  --box-pd: 37px 1rem;

  @media (min-width: $lg) {
    --box-pd: 0 30px 37px;
  }
  @media (min-width: $xl) {
    --box-pd: 0 55px 37px 37px;
  }
}

.box {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  border-radius: $borderRadius;
  box-shadow: $boxShadow;
  background-color: $blue-color;

  width: 100%;

  text-decoration: none;
  color: $primary-color;

  &:focus,
  &:hover {
    color: $primary-color;
    text-decoration: none;
  }

  @media (min-width: $xs) {
    // width: 40vw;
  }
  @media (min-width: $md) {
    // padding: 3.4rem 2.7rem 1rem 2rem;
  }
  @media (min-width: $lg) {
    margin-top: 0;
    width: 50%;

    padding: var(--box-pd);
  }

  @media (min-width: $xl) {
    min-width: 295px;
  }

  &--job {
    padding-top: calc(var(--box-pd-base) + 39px);

    @media (min-width: $sm) and (max-width: 800px) {
      max-width: 320px;
    }
  }

  &--schedule {
    padding-top: calc(var(--box-pd-base) + 32px);

    @media (min-width: $sm) and (max-width: 800px) {
      max-width: 320px;
    }
  }

  &__title {
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 20px;
    line-height: 130%;
    width: 100%;
    max-width: 220px;

    margin: 0;
    padding-bottom: 35px;

    & span {
      display: block;
      text-transform: uppercase;
    }
  }

  &__link {
    margin-left: auto;
    margin-right: 0;

    & span {
      margin: 0;
    }
  }

  &:focus,
  &:hover {
    background-color: $yellow-color;

    & .link-text--more svg {
      width: 40px;
      transform: translateX(6px);
      transition: 0.3s;
    }
  }

  &--yellow {
    background-color: $yellow-color;

    &:focus,
    &:hover {
      background-color: $blue-color;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    width: 100%;
    padding: 26px 57px 29px 33px;

    @media (min-width: $lg) {
      padding: 0;
    }
  }
}
