.definition-city {
  z-index: 10;
  position: absolute;
  top: 0;
  left: 10px;
  background-color: $yellow-color;
  border-radius: 14px;

  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;

  max-width: 310px;

  padding: 1rem 1rem 1.3rem 1.4rem;

  @media (min-width: $md) {
    top: 109px;
    left: 77px;
  }

  & h3 {
    width: 100%;
    margin-bottom: 0.8rem;
    font-family: $mainFontBold;
    font-style: normal;
    font-weight: 700;
    font-size: 15px;
    line-height: 140%;
    text-transform: uppercase;
    color: $primary-color;

    text-align: center;
  }

  & .button {
    margin: 0 0.5rem;

    &:active,
    &:focus,
    &:hover {
      background-color: $red-color;
      border-color: $red-color;
      color: $white-color;
    }
  }

  & .active.button {
    background-color: $primary-color;
    color: $white-color;
  }
}
