:root {
  --field-width: 100%;

  @media (min-width: $md) {
    --field-width: calc(50% - 0.5rem);
  }
}
.form {
  display: flex;
  flex-direction: column;

  &__check {
    display: flex;
    align-items: center;
    margin-top: 0.7rem;
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    line-height: 140%;
    color: #8f8f8f;
    cursor: pointer;

    & span {
      padding-left: 1rem;
    }

    @media (min-width: $md) {
      margin-top: 0.5rem;
      // justify-content: end;
    }
    @media (min-width: $xl) {
      justify-content: stretch;
    }
  }

  & .form-label,
  & .form-select {
    font-family: $mainFont;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 140%;
    color: $primary-color;
    @include px-to-rem(16, margin-bottom);

    outline-color: $primary-color;

    & span {
      display: block;
      margin-bottom: 12px;

      @media (min-width: $xl) {
        margin-bottom: 8px;
      }
    }
  }

  & .form-select,
  & .form-control {
    border-radius: 6px;
    border-color: transparent;
    padding: 0.775rem 0.75rem;

    @media (min-width: $xs) {
      padding: 0.375rem 0.75rem;
    }

    @media (min-width: $md) {
      padding: 0.775rem 1.25rem;
    }

    @media (min-width: $xl) {
      padding: 0.5rem 1.25rem;
    }

    cursor: pointer;
    &:focus,
    &:hover {
      border-color: $primary-color;
      box-shadow: none;
    }

    &::placeholder {
      font-family: $mainFont;
      font-style: normal;
      font-weight: 400;
      font-size: 20px;
      line-height: 130%;
      color: rgba(66, 71, 77, 0.5);
    }
  }

  & button[type='submit'] {
    margin-right: auto;
  }

  & .form-check-input:checked {
    background-color: #888888;
    border-color: #888888;
    margin: 0;
  }

  &--base {
  }

  &--offers {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1.3rem 0.7rem 1.1rem;
    background-color: $primary-color;
    border-radius: 22px;

    @media (min-width: $sm) {
      padding: 1.3rem 1.6rem 1.7rem;
    }

    @media (min-width: $xl) {
      padding: 1.5rem 1.5rem 1.1rem;
      // max-height: 312px;
    }

    & .form__title {
      margin-bottom: 1.2rem;
      font-family: $mainFontBold;
      font-weight: 700;
      font-size: 17px;
      line-height: 120%;
      color: $white-color;

      @media (min-width: $sm) {
        margin-bottom: 2rem;
      }

      @media (min-width: $xl) {
        margin-bottom: 1rem;
      }
    }
    & .form__wrapper {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;

      align-items: flex-start;

      @media (min-width: $md) {
        justify-content: space-between;
      }

      @media (min-width: $xl) {
        padding-top: 10px;
      }
    }

    & .form-label {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      width: var(--field-width);
      margin-bottom: 1.2rem;

      @media (min-width: $xs) {
        width: calc(50% - 0.5rem);
        margin-right: 8px;
      }

      @media (min-width: $sm) {
        width: calc(50% - 2.1rem);

        margin-right: 0.7rem;
      }
      @media (min-width: $md) {
        width: calc(50% - 0.5rem);

        margin-right: 0;
      }

      @media (min-width: $xxl) {
        width: calc(50% - 0.5rem);
        margin-bottom: 1rem;
      }
    }

    & .form-label span {
      color: $white-color;

      @media (min-width: $sm) {
        margin-bottom: 6px;
      }
    }

    & .form-control {
      align-items: flex-start;

      @media (min-width: $sm) {
        padding-left: 1.35rem;
      }
    }

    & .form-select {
      margin-bottom: 0;
      min-height: 46px;

      font-family: $mainFont;
      font-style: normal;
      font-weight: 400;
      font-size: 20px;
      line-height: 130%;
      color: rgba(66, 71, 77, 0.5);

      @media (min-width: $xl) {
        padding: 0.375rem 0.75rem;
      }
    }

    & .form__btns {
      display: flex;
      flex-direction: column;
      align-items: start;
      width: var(--field-width);
      padding-left: 10px;

      @media (min-width: $xs) {
        width: calc(50% - 0.5rem);
      }

      @media (min-width: $sm) {
        padding-top: 0.8rem;
      }

      @media (min-width: $md) {
        width: calc(50% - 0.5rem);
        padding-top: 1.4rem;
      }

      @media (min-width: 1024px) {
        padding-top: 0;
      }

      @media (min-width: $xxl) {
        width: calc(50% - 1rem);
        padding-right: 1.4rem;
        padding-left: 0;
        padding-top: 0.5rem;
      }
    }

    & button[type='submit'] {
      margin-top: 5px;
      margin-left: auto;
      margin-right: 0;
      padding: 23px 29px;

      @media (min-width: $xs) {
        padding: 19px 29px;
      }

      @media (min-width: $sm) {
        margin-right: 1.7rem;
        padding: 13px 69px;
      }

      @media (min-width: $md) {
        margin-right: 1.7rem;
        padding: 13px 39px;
      }

      @media (min-width: $xl) {
        margin-top: 20px;
        width: 100%;
        margin-left: 0;
        padding: 14px 20px;
        padding-right: 2.6rem;
      }
    }

    & .form-check-label {
      @media (min-width: $sm) {
        margin-left: auto;
        margin-right: 33px;
      }
      @media (min-width: $md) {
        margin-right: 0;
      }
    }
  }
}
