.modal-tour {
  & .modal-content {
    background-color: $yellow-color;
    border-radius: $borderRadius;

    @media (min-width: $lg) {
      background-image: url(../img/modal/modal-bg-gym.jpg);
      background-repeat: no-repeat;
      background-position: 100% 0;
      background-size: 45% 100%;
    }
  }

  & .modal-title {
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 20px;
    line-height: 130%;
  }

  & .modal-header {
    border: none;
    font-family: $mainFont;
    font-weight: 700;

    @media (min-width: $xl) {
      padding-left: 57px;
      padding-top: 50px;
    }
  }

  & .btn-close {
    @include buttonReset();
    opacity: 1;
    color: $white-color;
    background: none;
  }

  & .modal-body {
    display: flex;
    flex-direction: column;

    @media (min-width: $xl) {
      padding-left: 57px;
      padding-bottom: 50px;
      width: 50%;
    }
  }


}
