.modal.modal-city {
  background-color: transparent;

  &--list {
    background-color: rgba(66, 71, 77, 0.7);;
  }

  & .modal-dialog {
    max-width: 400px;
  }

  & .modal-content {
    padding: 1rem;
    font-family: $mainFont;
    background-color: $yellow-color;
  }

  & .modal-header {
    border: none;
    padding: 0;
    align-items: baseline;
  }

  & .modal-body {
    padding: 0;
    border-color: transparent;
  }

  & .modal-city-footer {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

    & .button {
      margin: 0 0.5rem;
    }
  }

  & .modal-title {
    margin-bottom: 1rem;
    font-family: $mainFontBold;
    font-weight: 700;
    font-size: 15px;
    line-height: 140%;
    text-transform: uppercase;
    color: $primary-color;
  }

  & .btn-close {
    padding: 0;
    margin: 0;
    background: transparent;
    background-color: transparent;
    border: 0;
  }
}
