.page-header {
  position: relative;
  background-color: $primary-color;
  padding: 1.8rem 0 0.8rem 0;
  transition: 0.5s;

  @media (min-width: $sm) {
    padding: 1.4rem 0 1.6rem;
  }
  @media (min-width: $md) {
    padding: 1.3rem 0 1.1rem;
  }
  @media (min-width: $xl) {
    padding: 1.7rem 0 1.6rem;
  }

  & .container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    flex-direction: row-reverse;

    @media (min-width: $md) {
      flex-direction: row;
    }
  }

  & a {
    align-items: center;
    color: $white-color;
  }

  & .phone {
    width: 30px;

    @media (min-width: $sm) {
      width: 50px;
    }
    @media (min-width: $md) {
      width: auto;
    }

    & span {
      display: none;

      @media (min-width: $md) {
        display: flex;
      }
    }

    & .icon {
      margin-right: 0;
      margin-left: auto;

      @media (min-width: $sm) {
        width: 14px;
        height: 24px;
      }
    }
  }

  &__lk {
    display: none;
    @media (min-width: $md) {
      display: flex;
    }

    & span {
      margin-left: 0;
    }
  }

  & .logo {
    @media (min-width: $sm) {
      width: 230px;
      // margin-left: -15px;
    }

    @media (min-width: $md) {
      width: auto;
      height: auto;
      margin-left: 0;
    }
    @media (min-width: $xl) {
      width: auto;
      margin-left: 20px;
    }
  }

  &__btn-menu {
    @include buttonReset();
    display: flex;
    color: $white-color;

    @media (min-width: $md) {
      display: none;
    }

    & .icon {
      width: 30px;
      height: 30px;
      fill: $white-color;

      @media (min-width: $sm) {
        width: 50px;
      }
    }
  }
}
